"use client";

import { useCallback, useEffect, useMemo, useState } from "react";
import { AffiliateAPI } from "@/utils/api";
import { useToast } from "@/components/ui/toast";

export type DashboardSummary = {
  referrals_count: number;
  amount: number;
  percent: number;
  year: number;
  month: number;
  tenants: string[];
  payouts: { pending_total: number; paid_total: number };
};

export type ReferralLink = { token: string; url: string; active: boolean };

export type AffiliateStats = {
  referrals: number;
  currentCommission: number;
  pendingPayout: number;
  paidPayout: number;
};

export function useAffiliateDashboard() {
  const { show } = useToast();
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [dash, setDash] = useState<DashboardSummary | null>(null);
  const [links, setLinks] = useState<ReferralLink[]>([]);
  const [canCreateMore, setCanCreateMore] = useState(true);
  const [payoutMonth, setPayoutMonth] = useState("");
  const [payoutPercent, setPayoutPercent] = useState<number>(5);
  const [requestingPayout, setRequestingPayout] = useState(false);

  const refreshAll = useCallback(async () => {
    setLoading(true);
    try {
      const [dashboard, linkPayload] = await Promise.all([
        AffiliateAPI.dashboard(),
        AffiliateAPI.getLinks(),
      ]);
      const dashData = dashboard as DashboardSummary;
      setDash(dashData);
      if (dashData?.year && dashData?.month) {
        const monthStr = `${dashData.year}-${String(dashData.month).padStart(2, "0")}`;
        setPayoutMonth(monthStr);
      }
      if (dashData?.percent) {
        setPayoutPercent(dashData.percent);
      }
      const linkData = (linkPayload as any) || {};
      setLinks(linkData.links || []);
      setCanCreateMore(Boolean(linkData.can_create_more));
      setError(null);
    } catch (e: any) {
      setError(e?.message || "Failed to load affiliate data");
      show({
        variant: "destructive",
        title: "Error",
        description: e?.message || "Failed to load affiliate data",
      });
    } finally {
      setLoading(false);
    }
  }, [show]);

  useEffect(() => {
    refreshAll();
  }, [refreshAll]);

  const stats: AffiliateStats = useMemo(
    () => ({
      referrals: dash?.referrals_count || 0,
      currentCommission: dash?.amount || 0,
      pendingPayout: dash?.payouts?.pending_total || 0,
      paidPayout: dash?.payouts?.paid_total || 0,
    }),
    [dash]
  );

  const monthLabel = useMemo(() => {
    if (dash?.month && dash?.year) {
      return `${String(dash.month).padStart(2, "0")}/${dash.year}`;
    }
    return "Current period";
  }, [dash?.month, dash?.year]);

  const handleCreateLink = useCallback(async () => {
    try {
      await AffiliateAPI.createLink();
      show({ variant: "success", title: "Link created" });
      await refreshAll();
    } catch (e: any) {
      show({ variant: "destructive", title: "Failed to create link", description: e?.message });
    }
  }, [refreshAll, show]);

  const handleDeactivate = useCallback(
    async (token: string) => {
      try {
        await AffiliateAPI.deactivate(token);
        show({ variant: "success", title: "Link deactivated" });
        await refreshAll();
      } catch (e: any) {
        show({ variant: "destructive", title: "Failed to deactivate", description: e?.message });
      }
    },
    [refreshAll, show]
  );

  const handleRotate = useCallback(
    async (token: string) => {
      try {
        await AffiliateAPI.rotate(token);
        show({ variant: "success", title: "Link rotated" });
        await refreshAll();
      } catch (e: any) {
        show({ variant: "destructive", title: "Failed to rotate", description: e?.message });
      }
    },
    [refreshAll, show]
  );

  const handleCopy = useCallback(
    async (url: string) => {
      try {
        if (typeof navigator === "undefined" || !navigator.clipboard) {
          throw new Error("Clipboard not available in this environment");
        }
        await navigator.clipboard.writeText(url);
        show({ variant: "success", title: "Link copied", description: "Referral URL saved to clipboard." });
      } catch (e: any) {
        show({
          variant: "destructive",
          title: "Could not copy",
          description: e?.message || "Clipboard action blocked by your browser.",
        });
      }
    },
    [show]
  );

  const handleRequestPayout = useCallback(async () => {
    if (!payoutMonth) {
      show({ variant: "destructive", title: "Missing month", description: "Select a month to request payment for." });
      return;
    }
    setRequestingPayout(true);
    try {
      await AffiliateAPI.requestPayout(payoutMonth, payoutPercent || 5);
      show({
        variant: "success",
        title: "Payout requested",
        description: "We will review and notify you once processed.",
      });
      await refreshAll();
    } catch (e: any) {
      show({
        variant: "destructive",
        title: "Failed to request payout",
        description: e?.message || "Try again later",
      });
    } finally {
      setRequestingPayout(false);
    }
  }, [payoutMonth, payoutPercent, refreshAll, show]);

  return {
    loading,
    error,
    dash,
    stats,
    links,
    canCreateMore,
    payoutMonth,
    payoutPercent,
    requestingPayout,
    monthLabel,
    setPayoutMonth,
    setPayoutPercent,
    refreshAll,
    actions: {
      createLink: handleCreateLink,
      deactivate: handleDeactivate,
      rotate: handleRotate,
      copyLink: handleCopy,
      requestPayout: handleRequestPayout,
    },
  } as const;
}
