"use client";

import React from "react";
import { Skeleton } from "@/components/ui/skeleton";
import { AffiliateSidebar } from "./_components/sidebar";
import { useAffiliateDashboard } from "./_hooks/use-affiliate-dashboard";
import { StatCard, MiniStat, HighlightPill, SectionCard } from "./_components/cards";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Copy, DollarSign, Link2, Sparkles, Users, Wallet } from "lucide-react";

function formatCurrency(amount: number) {
  return new Intl.NumberFormat("en-US", { style: "currency", currency: "USD" }).format(amount || 0);
}

export default function AffiliateOverviewPage() {
  const {
    loading,
    error,
    stats,
    dash,
    links,
    canCreateMore,
    payoutMonth,
    payoutPercent,
    requestingPayout,
    monthLabel,
    setPayoutMonth,
    setPayoutPercent,
    actions,
  } = useAffiliateDashboard();

  if (loading) {
    return (
      <div className="grid gap-6 lg:grid-cols-[minmax(0,260px),minmax(0,1fr)]">
        <div className="space-y-4">
          <Skeleton className="h-48 rounded-3xl" />
          <Skeleton className="h-56 rounded-3xl" />
          <Skeleton className="h-56 rounded-3xl" />
        </div>
        <div className="space-y-4">
          <Skeleton className="h-48 rounded-3xl" />
          <Skeleton className="h-32 rounded-3xl" />
          <Skeleton className="h-96 rounded-3xl" />
        </div>
      </div>
    );
  }

  if (error) {
    return <div className="text-sm text-red-600">{error}</div>;
  }

  return (
    <div className="relative mx-auto grid max-w-6xl gap-8 lg:grid-cols-[minmax(0,280px),minmax(0,1fr)]">
      <AffiliateSidebar stats={stats} monthLabel={monthLabel} payoutPercent={payoutPercent} />

      <div className="space-y-8 pb-20">
        <SectionCard title="Commission snapshot" description={`Earnings update live for ${monthLabel.toLowerCase()}.`}>
          <div className="grid gap-4 lg:grid-cols-4">
            <div className="rounded-2xl bg-gradient-to-br from-emerald-500 via-emerald-600 to-emerald-700 p-6 text-white lg:col-span-2">
              <div className="text-xs uppercase tracking-widest text-emerald-50/80">This month</div>
              <div className="mt-3 text-3xl font-semibold">{formatCurrency(stats.currentCommission)}</div>
              <div className="mt-4 flex flex-wrap gap-2 text-xs text-emerald-50/90">
                <span className="inline-flex items-center gap-2 rounded-full bg-white/15 px-3 py-1">
                  <Wallet className="h-4 w-4" /> Commission rate {dash?.percent ?? payoutPercent}%
                </span>
                <span className="inline-flex items-center gap-2 rounded-full bg-white/15 px-3 py-1">
                  <Sparkles className="h-4 w-4" /> Pending review {formatCurrency(stats.pendingPayout)}
                </span>
              </div>
            </div>
            <StatCard
              title="Referrals"
              value={stats.referrals.toLocaleString()}
              description="Pharmacies onboarded through your links"
              icon={<Users className="h-5 w-5" />}
            />
            <StatCard
              title="Paid to date"
              value={formatCurrency(stats.paidPayout)}
              description="Total cleared to your account"
              icon={<DollarSign className="h-5 w-5" />}
            />
          </div>
        </SectionCard>

        <SectionCard title="This month at a glance" description={`Key activity in ${monthLabel.toLowerCase()}.`}>
          <div className="grid gap-4 lg:grid-cols-2">
            <div className="space-y-4">
              <div className="grid gap-3 sm:grid-cols-3">
                <MiniStat label="Commission rate" value={`${dash?.percent ?? payoutPercent}%`} />
                <MiniStat label="Earned" value={formatCurrency(dash?.amount || 0)} />
                <MiniStat label="New pharmacies" value={(dash?.tenants?.length || 0).toString()} />
              </div>
              <div className="rounded-2xl border border-dashed border-emerald-200 bg-emerald-50/70 p-4">
                <p className="text-xs font-medium text-emerald-700/80">Tenant IDs credited this month</p>
                <div className="mt-3 flex max-h-32 flex-wrap gap-2 overflow-auto pr-1">
                  {(dash?.tenants || []).length === 0 ? (
                    <span className="rounded-full bg-white px-3 py-1 text-xs font-medium text-emerald-600 shadow-sm">
                      No pharmacies have been credited yet.
                    </span>
                  ) : (
                    dash?.tenants?.map((tenant) => (
                      <span key={tenant} className="rounded-full bg-white px-3 py-1 text-xs font-medium text-emerald-700 shadow-sm">
                        {tenant}
                      </span>
                    ))
                  )}
                </div>
              </div>
            </div>

            <div className="rounded-2xl border border-emerald-100/80 bg-gradient-to-br from-white via-emerald-50/60 to-white p-6">
              <div className="space-y-4">
                <div>
                  <p className="text-xs font-medium uppercase tracking-wide text-emerald-500">Ready for a payout?</p>
                  <p className="text-sm text-emerald-700/80">Submit a commission payout request in minutes.</p>
                </div>
                <div className="space-y-4">
                  <div className="space-y-2">
                    <label className="text-xs font-medium text-emerald-600">Month (YYYY-MM)</label>
                    <Input
                      value={payoutMonth}
                      onChange={(e) => setPayoutMonth(e.target.value)}
                      placeholder="2025-10"
                      className="h-11 rounded-xl border-emerald-200 bg-white/85 focus:border-emerald-400 focus:ring-emerald-400"
                    />
                  </div>
                  <div className="space-y-2">
                    <label className="text-xs font-medium text-emerald-600">Commission percent</label>
                    <Input
                      type="number"
                      min={1}
                      max={20}
                      step={0.5}
                      value={payoutPercent}
                      onChange={(e) => setPayoutPercent(Number(e.target.value))}
                      className="h-11 rounded-xl border-emerald-200 bg-white/85 focus:border-emerald-400 focus:ring-emerald-400"
                    />
                  </div>
                  <Button
                    onClick={actions.requestPayout}
                    disabled={requestingPayout}
                    className="h-11 w-full rounded-xl bg-emerald-600 text-sm font-semibold text-white shadow-lg transition hover:bg-emerald-700 disabled:cursor-not-allowed disabled:bg-emerald-200"
                  >
                    {requestingPayout ? "Submitting..." : "Submit payout request"}
                  </Button>
                  <p className="text-xs text-emerald-600/80">
                    Our finance team reviews payout requests within two business days. You&apos;ll get an email once it&apos;s processed.
                  </p>
                </div>
              </div>
            </div>
          </div>
        </SectionCard>

        <SectionCard title="Referral links" description="Create, rotate, and deactivate your referral URLs.">
          <div className="mb-4 flex flex-wrap items-center justify-between gap-3">
            <div className="text-xs font-medium text-emerald-600">You can keep up to 2 links active at once.</div>
            <Button
              onClick={actions.createLink}
              disabled={!canCreateMore}
              className="h-10 rounded-xl bg-emerald-600 px-6 text-sm font-semibold text-white shadow-lg transition hover:bg-emerald-700 disabled:cursor-not-allowed disabled:bg-emerald-200"
            >
              {canCreateMore ? "Create new referral link" : "Max 2 active links reached"}
            </Button>
          </div>
          <div className="divide-y divide-emerald-100/70">
            {links.map((l) => (
              <div key={l.token} className="flex flex-col gap-4 py-5 sm:flex-row sm:items-center sm:justify-between">
                <div className="space-y-2">
                  <div className="flex items-center gap-2 text-xs font-medium text-emerald-600">
                    <span
                      className={`inline-flex items-center gap-2 rounded-full px-3 py-1 text-xs font-semibold ${
                        l.active ? "bg-emerald-100 text-emerald-700" : "bg-gray-100 text-gray-600"
                      }`}
                    >
                      <span className="h-2.5 w-2.5 rounded-full bg-current" />
                      {l.active ? "Active" : "Inactive"}
                    </span>
                    <span className="font-mono text-[11px] uppercase tracking-widest text-emerald-500/80">
                      {l.token.slice(0, 10)}
                    </span>
                  </div>
                  <div className="flex items-center gap-2 text-sm font-medium text-emerald-900">
                    <Link2 className="h-4 w-4 text-emerald-500" />
                    <span className="break-all font-mono text-xs text-emerald-800/90">{l.url}</span>
                  </div>
                </div>
                <div className="flex flex-wrap gap-2">
                  <Button
                    size="sm"
                    variant="outline"
                    className="rounded-lg border-emerald-200 text-emerald-700 hover:bg-emerald-50"
                    onClick={() => actions.copyLink(l.url)}
                  >
                    <Copy className="mr-2 h-4 w-4" /> Copy
                  </Button>
                  <Button
                    size="sm"
                    variant="outline"
                    className="rounded-lg border-emerald-200 text-emerald-700 hover:bg-emerald-50 disabled:border-gray-200 disabled:text-gray-400"
                    onClick={() => actions.deactivate(l.token)}
                    disabled={!l.active}
                  >
                    Deactivate
                  </Button>
                  <Button
                    size="sm"
                    variant="outline"
                    className="rounded-lg border-emerald-200 text-emerald-700 hover:bg-emerald-50"
                    onClick={() => actions.rotate(l.token)}
                  >
                    Rotate
                  </Button>
                </div>
              </div>
            ))}
            {links.length === 0 && (
              <div className="py-10 text-center text-sm text-emerald-700/70">
                No referral links yet. Use the button above to create one and start sharing.
              </div>
            )}
          </div>
        </SectionCard>
      </div>
    </div>
  );
}
